import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

export default function BasicSelect({year, setyear}) {

  const handleChange = (event) => {
    setyear(event.target.value);
  };

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Grado Acadmico</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="seccionSeclectYear"
          value={year}
          label="Grado Acadmico"
          onChange={handleChange}
        >
          <MenuItem value={1}>Primer ao</MenuItem>
          <MenuItem value={2}>Segundo ao</MenuItem>
          <MenuItem value={3}>Tercer ao</MenuItem>
          <MenuItem value={4}>Cuarto ao</MenuItem>
          <MenuItem value={5}>Quinto ao</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
}